.seeAllPage {
  .friendsWidget {
    .media {
      display: flex;
      align-items: center;
    }
    .widgetAvatar {
      width: 2rem !important;
      height: auto !important;
    }
    .media-heading {
      font-size: 14px !important;
    }
    .media-subheading {
      color: #003787 !important;
    }
    .media-body {
      width: 100px !important;
      max-height: 40px;
      margin: 0 10px;
      overflow: hidden;
      text-overflow: ellipsis;
      overflow-wrap: break-word;
      white-space: break-spaces;
    }
    .media-right {
      align-items: flex-end;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .friend-search {
      margin: 0 auto;
      border-left: 1px solid #ccd4ff;
      input {
        border: none;
      }
      button {
        background-color: transparent;
      }
    }

    .list-group .list-group-item .media-right {
      height: auto;
    }
  }
  .authorBio {
    img {
      width: 50px;
      border-radius: 5px;
    }
    .media {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    p {
      font-size: 14px !important;
      line-height: 16px;
    }
  }
}
.thumbnail {
  box-shadow: 0 0 3px #00000099;
  border-radius: 2px;
  width: 35px;
  height: 35px;
  background-color: #555;
  background-size: cover;
  background-position: center center;
}
